import { vaultProjectData } from "data/vaultProject";
import moment from "moment";

const VaultProject = () => {
  return (
    <div className="vault-project-container">
      <div className="table-header">
        <div className="period">
          <p>수행 기간</p>
        </div>
        <div className="title">
          <p>프로젝트명</p>
        </div>
        <div className="type">
          <p>프로젝트 단위 / 유형</p>
        </div>
        <div className="status">
          <p>진행 / 운영 상태</p>
        </div>
      </div>

      {vaultProjectData.map((item) => (
        <div key={item.id} className="each-table">
          <div className="period">
            <p>
              {moment(item.startDate).format("YYYY-MM-DD")}
              <br />~{" "}
              {moment(item.endDate).format("YYYY-MM-DD") ===
              moment().format("YYYY-MM-DD") ? (
                <span>현재</span>
              ) : (
                moment(item.endDate).format("YYYY-MM-DD")
              )}
            </p>
          </div>

          <div className="title">
            <p>{item.name}</p>
          </div>

          <div className="type">
            <p>
              {item.unit.split("/")[0] === "corporation"
                ? "기업"
                : item.unit.split("/")[0] === "team"
                ? item.unit.split("/")[1] === "school"
                  ? "교내"
                  : "외부"
                : "개인"}{" "}
              /{" "}
              {item.type.split("/")[0] === "prototype"
                ? "프로토타입"
                : item.type.split("/")[0] === "service"
                ? "서비스"
                : "연구개발"}
            </p>
          </div>

          <div className="status">
            <p>
              {item.status === "done" ? (
                "완료"
              ) : item.status === "paused" ? (
                "중단"
              ) : (
                <span>진행중</span>
              )}{" "}
              /{" "}
              {item.type.split("/")[1] === "use" ? (
                <span>운영중</span>
              ) : item.type.split("/")[1] === "futureuse" ? (
                "운영 예정"
              ) : item.type.split("/")[0] === "prototype" ? (
                "사용 중단"
              ) : item.type.split("/")[0] === "rnd" ? (
                "연구 종료"
              ) : (
                "운영 종료"
              )}
            </p>
          </div>

          <div className="detail">
            <p>자세히 보기</p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default VaultProject;
