import {
  iconBoj,
  iconDbpia,
  iconGithub,
  iconGmail,
  iconInstagramColor,
  iconKakaoMail,
  iconNaverMail,
} from "asset/exportImages";

export const ProfileContact = ({
  href,
  title,
  noMR,
}: {
  href: string;
  title: string;
  noMR?: boolean;
}) => {
  return (
    <a href={href} target="_blank" rel="noreferrer">
      <img
        src={
          title === "gmail"
            ? iconGmail
            : title === "naver-mail"
            ? iconNaverMail
            : title === "kakao-mail"
            ? iconKakaoMail
            : title === "github"
            ? iconGithub
            : title === "instagram"
            ? iconInstagramColor
            : title === "boj"
            ? iconBoj
            : iconDbpia
        }
        width="17"
        height="17"
        alt=""
        style={{ cursor: "pointer", marginRight: noMR ? 0 : 10 }}
      />
    </a>
  );
};
